<template>
  <div class="home">
    <div class="row" style="height: 5.4rem;">
      <div class="column" style="flex-direction: column;background: #fff;width: 1rem;">
        <div class="son space" style="height: 0.84rem;"></div>
        <div class="son flex space"></div>
        <div class="son" style="height: 1.2rem;background: #fff1f1;" @click="show(0)">玄关</div>
      </div>
      <div class="column" style="flex-direction: column;background: #fff;width: 4rem;">
        <div class="son" style="height: 0.84rem;background: #fff1f1;" @click="show(1)">阳台</div>
        <div class="son flex" style="background: #bfefca;" @click="show(2)">客厅</div>
        <div class="son" style="height: 1.2rem;background: #bfefca;" @click="show(3)">餐厅</div>
      </div>
      <div class="column flex" style="flex-direction: column;background: #fff;">
        <div class="son flex" style="background: #efc4f7;" @click="show(4)">主卧</div>
        <div class="son" style="height: 1.2rem;background: #bfefca;" @click="show(5)">厕所</div>
        <div class="son flex" style="background: #efc4f7;" @click="show(6)">次卧</div>
      </div>
    </div>

    <div class="row" style="height: 2rem;">
      <div class="column" style="width: 1.38rem;background: #bfefca;" @click="show(7)">厨房</div>
      <div class="column flex" style="background: #efc4f7;" @click="show(8)">客房</div>
      <div class="column" style="width: 1.38rem;background: #bfefca;" @click="show(9)">厕所</div>
      <div class="column flex" style="background: #efc4f7;" @click="show(10)">书房</div>
    </div>

    <light ref="light"></light>
  </div>
</template>

<script>
import Light from './light'
import json from '../js/json'

export default {
  name: 'home',
  data () {
    return {
    }
  },
  components: { Light },
  methods: {
    show(n){
      var item = json[n];
      this.$refs.light.alert(item);
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="stylus" scoped>
.home{
  width: 7rem;
  margin: auto;
}
.row{
  display: flex;
  width: 100%;
  margin-bottom: 0.04rem;
}
.flex{
  flex: 1;
}
.column{
  display: flex;
  color: #000;
  font-size: 0.24rem;
  background: #d8eaac;
  justify-content: center;
  align-items: center;
  position: relative;
  margin-right: 0.04rem;
}
.column:last-child{
  margin-right: 0;
}
.son{
  display: flex;
  color: #000;
  width: 100%;
  font-size: 0.24rem;
  background: #909090;
  justify-content: center;
  align-items: center;
  position: relative;
  margin-bottom: 0.04rem;
}
.son:last-child{
  margin-bottom: 0;
}
.space{
  background: #fff;
}
</style>



