<template>
  <div class="list">
    <div class="item" v-for="item in list">
      <img class="img" :src="item.src|link">
      <div class="content">
        <div class="tip">参数：{{item.info}}</div>
        <div class="tip">用途：{{item.place}}</div>
        <div class="tip">功率：{{item.power}}瓦</div>
        <div class="price">￥{{item.price}}</div>
        <div class="num">X{{item.num}}</div>
      </div>
    </div>
    <div class="all">
        <div class="price">总价：￥{{price}}</div>
        <div class="num">合计：{{total}}</div>
    </div>
  </div>
</template>

<script>

import json from '../js/json'

function has(item, list){
  for(var i = 0; i < list.length; i++){
    if(item.price == list[i].price && item.src == list[i].src){
      return list[i];
    }
  }
  return false;
}

export default {
  name: 'list',
  data () {
    return {
      
    }
  },
  computed: {
    list: function(){
      var aim = [];      
      for(var i in json){
        var temp = json[i];
        var result = has(temp, aim);
        if(result){
          if(result.place != temp.place){
            result.place = result.place + "|" + temp.place;
          }
          result.num += 1;
        }
        else{
          temp.num = 1;
          aim.push(temp);
        }
      }
      return aim;
    },
    total: function(){
      var n = 0;
      for(var i in json){
        n++;
      }
      return n;
    },
    price: function(){
      var n = 0;
      for(var i in json){
        n += parseFloat(json[i].price);
      }
      n = Math.ceil(n * 100) / 100;
      return n;
    }
  },
  methods: {
    
  },
  filters: {
    link:function(str){
      return "static/img/" + str;
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="stylus" scoped>
.list{
  padding: 0.24rem;
}
.item {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  border-bottom: solid 1px #e9e9e9;
  padding-bottom: 0.12rem;
  margin-bottom: 0.1rem;
}
.img{
  width: 1.48rem;
  margin-right: 0.24rem;
}
.content{
  position: relative;
  flex: 1;
}
.tip{
  margin-top: 0.1rem;
  text-align: left;
}
.price{
  position: absolute;
  right: 0.2rem;
  top: 0.15rem;
  color: #900;
  font-size: 0.28rem;
}
.num{
  position: absolute;
  right: 0.2rem;
  bottom: 0.15rem;
  font-size: 0.24rem;
}
.all{
  width: 100%;
  height: 1rem;
  position: relative;
  margin-bottom: 0.2rem;
}
</style>



