<template>
  <div id="app">
    <div class="nav">
      <div class="title" @click="chose(0)" :class="{chose:select==0}">户型灯饰布局</div>
      <div class="title" @click="chose(1)" :class="{chose:select==1}">灯饰列表</div>
    </div>
    <home v-show="select==0"></home>
    <list v-show="select==1"></list>
  </div>
</template>

<script>
import Home from './components/home'
import List from './components/list'

export default {
  name: 'app',
  components: {
    Home, List
  },
  data(){
    return {
      select: 0
    }    
  },
  methods: {
    chose: function(n){
      this.select = n;
    }
  }
}
</script>

<style lang="stylus">
html, body{
  padding: 0;
  margin: 0;
}
#app {
  font-family: "Microsoft YaHei", STHeiti, Tahoma, Helvetica, sans-serif;
  text-align: center;
  color: #2c3e50;
  font-size: 0.18rem;
}
.nav{
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 0.2rem;
}
.title{
  background: #d0d0d0;
  flex: 1;
  font-size: 0.32rem;
  padding: 0.24rem;
  text-align: center;
  cursor: pointer;
}
.chose{
  background: #fff;
  color: #6AA9EC;
}
</style>



