<template>
  <div class="light" v-show="show">
    <div class="content">
      <div class="close" @click="hide">X</div>
      <img class="img" :src="item.src|link">
      <div class="tip">参数：{{item.info}}</div>
      <div class="tip">用途：{{item.place}}</div>
      <div class="tip">功率：{{item.power}}瓦</div>
      <div class="price">￥{{item.price}}</div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'light',
  props:['info'],
  data () {
    return {
      show: false,
      item: {}
    }
  },
  methods: {
    hide(){
      this.show = false;
    },
    alert(item){
      this.show = true;
      this.item = item;
    }
  },
  filters: {
    link:function(str){
      return "static/img/" + str;
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="stylus" scoped>
.light {
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.84);
}
.content{
  width: 5rem;
  padding: 0.12rem;
  background: #fff;
  position: relative;
  top: -0.1rem;
}
.img{
  width: 100%;
}
.close{
  position: absolute;
  width: 0.48rem;
  height: 0.48rem;
  display: flex;
  background: #690;
  right: 0;
  top: -0.48rem;
  justify-content: center;
  align-items: center;
  font-size: 0.3rem;
  color: #fff;
  cursor: pointer;
}
.tip{
  margin-top: 0.1rem;
  text-align: left;
}
.price{
  position: absolute;
  right: 0.2rem;
  bottom: 0.15rem;
  color: #900;
  font-size: 0.28rem;
}
</style>



